import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';
import { AuthGuard } from 'src/guard/auth.guard';
import { DriverGuard } from 'src/guard/driver.guard';
import { PassengerGuard } from 'src/guard/passenger.guard';

const routes: Routes = [
  {
    path: 'account',
    component: TabsPage,
    children: [
      {
        path: 'driver',
        children: [
          {
            path: 'generate-code',
            canActivate: [AuthGuard, DriverGuard],
            loadChildren: () => import('../driver/generate-code/generate-code.module').then(m => m.GenerateCodePageModule)
          },
          {
            path: 'profile',
            canActivate: [AuthGuard, DriverGuard],
            loadChildren: () => import('../driver/profile/profile-routing.module').then(m => m.ProfilePageRoutingModule)
          },
          {
            path: 'my-wallet',
            canActivate: [AuthGuard, DriverGuard],
            loadChildren: () => import('../driver/wallet/wallet-routing.module').then(m => m.WalletPageRoutingModule)
          }
        ],
        // canActivate: [AuthGuard, DriverGuard]
      },
      {
        path: 'user',
        children: [
          {
            path: 'scan-code',
            canActivate: [AuthGuard, PassengerGuard],
            loadChildren: () => import('../passenger/passenger-home/passenger-home.module').then(m => m.PassengerHomePageModule)
          },
          {
            path: 'history',
            loadChildren: () => import('../passenger/passenger-transaction/passenger-transaction.module').then( m => m.PassengerTransactionPageModule)
          },
          {
            path: 'top-up',
            loadChildren: () => import('../passenger/passenger-topup/passenger-topup.module').then( m => m.PassengerTopupPageModule)
          },
          {
            path: 'profile',
            canActivate: [AuthGuard, DriverGuard],
            loadChildren: () => import('../driver/profile/profile-routing.module').then(m => m.ProfilePageRoutingModule)
         
          }
        ]
      },
    

      // // remove later on
      // {
      //   path: 'my-profile',
      //   // loadChildren: () => import('../user/my-profile/my-profile.module').then(m => m.MyProfilePageModule)
      //   loadChildren: () => import('../passenger/passenger-home/passenger-home.module').then( m => m.PassengerHomePageModule)
      // },
      // {
      //   path: 'history',
      //   loadChildren: () => import('../passenger/passenger-transaction/passenger-transaction.module').then(m => m.PassengerTransactionPageModule)
      // },


      // {
      //   path: '',
      //   redirectTo: '/tabs/scanCode',
      //   pathMatch: 'full'
      // }
    ]
  },
  {
    path: '',
    redirectTo: '/account/driver/generate-code',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
})
export class TabsPageRoutingModule {}
